export default Footer;

const MainContainer = styled.div`
  position: relative;
  overflow: hidden;
`;

const Overlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  backdrop-filter: blur(6px);
  background-color: rgba(0, 0, 0, 0.31);
  .over-lay{
    position: absolute;
    background: black;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0.7;

  }
`;

const SubContainer = styled.div`
  width: 100%;
  background-color: #da3b19;
  min-height: 88px;
`;
