import axios from 'axios';
import { FC } from 'react';
import { useQuery } from 'react-query';
import styled from 'styled-components';
import { websiteId } from '../../../baseURL';
import useLanguage from '../../../shared/hooks/useLanguage';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';

interface SubscriptionTypes {
  id: number;
  Price: number;
  PayFrequency: number;
  title: string;
  cancel_anytime: boolean;
  number_of_searches: number | null;
  referrel_fee: number;
}

const PlansSection: FC = () => {
  const { language } = useLanguage();

  const { data: subscriptionsList = [] } = useQuery(
    ['subscription_list', language, websiteId],
    () => axios.get(`/subscription`),
    {
      select: (res) => {
        return res.data.data;
      },
      enabled: !!language,
    }
  );

  const getFrequency = (freq: number) => {
    if (freq === 1) {
      return 'Per Month';
    }

    if (freq === 12) {
      return 'Per Year';
    }

    return `Every ${freq} months`;
  };

  return (
    <div className="mx-auto flex flex-col items-center mt-20 pb-28">
      <StyledText fs={29} fw={600} ls={0.87} lh={49} color="#DA3B19">
        New to CaringDay?
      </StyledText>
      <StyledText fs={20} fw={500} color="#727272" className="ml-4 sm:ml-0">
        Select the plan according to your needs.
      </StyledText>
      <div className="flex flex-col justify-center mx-auto  xl:flex-row items-center mt-10">
        {subscriptionsList?.map((val: SubscriptionTypes, idx: number) => (
          <SilverCard
            className={`${idx === 1 && 'mt-10'} sm:w-96 mx-4`}
            key={val.id}
          >
            <StyledButton className="px-8 py-5 w-full h-16" bg="#EA7112">
              <StyledText fs={20} fw={700} ls={1.4} color="white">
                {val.title}
              </StyledText>
            </StyledButton>
            <StyledText
              fs={55}
              fw={600}
              ls={0.87}
              color="#EA7112"
              className="mt-24 text-center"
            >
              {`$${val.Price}`}
            </StyledText>
            <StyledText
              fs={20}
              fw={500}
              color="#727272"
              className="mt-8 text-center"
            >
              {getFrequency(val.PayFrequency)}
            </StyledText>
            <StyledText fs={20} fw={500} lh={30} className="mt-10 text-center">
              {val.cancel_anytime ? (
                <li>
                  <span>Cancel anytime</span>
                </li>
              ) : null}
              {val.number_of_searches === null ? (
                <li>
                  <span>
                    Unlimited searches for
                    <br />
                    candidates
                  </span>
                </li>
              ) : (
                <li>
                  <span>
                    {val.number_of_searches} searches for
                    <br />
                    candidates
                  </span>
                </li>
              )}

              <li>
                <span>{`${
                  val.referrel_fee === 0 ? 'No' : val.referrel_fee
                } referral fees`}</span>
              </li>
            </StyledText>
            <StyledText
              fs={25}
              fw={600}
              ls={0.75}
              color="#DA3B19"
              className="mt-14 text-center"
            >
              SUBSCRIBE
            </StyledText>
          </SilverCard>
        ))}
      </div>
    </div>
  );
};

export default PlansSection;

const SilverCard = styled.div`
  max-width: 398px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #00000012;
  background: white;
  padding: 20px;
  margin-bottom: 20px;
  li {
    color: red;
  }
  span {
    color: black;
  }
`;
const GoldCard = styled.div`
  max-width: 398px;
  height: 595px;
  background: white;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000012;
  // margin: 0px 70px;
  padding: 20px;
  margin-bottom: 20px;
  li {
    color: red;
  }
  span {
    color: black;
  }
`;
const DiamondCard = styled.div`
  max-width: 354px;
  height: 528px;
  background: white;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000012;
  padding: 20px;
  margin-bottom: 20px;
  li {
    color: red;
  }
  span {
    color: black;
  }
`;
