import { FC, useEffect } from 'react';
import styled from 'styled-components';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import HookTextGlobalInput from '../../../shared/hookForm/HookTextGlobalInput';
import { get } from 'lodash';
import Checkbox from '../../../shared/form/Checkbox';
import _ from 'lodash';
import axios from 'axios';
import useLanguage from '../../../shared/hooks/useLanguage';
import { useSnackbar } from 'notistack';
import { useQuery } from 'react-query';
import { websiteId } from '../../../baseURL';

interface RegisterFormTypes {
  categories: number[];
  city: string;
  description: string;
}

const schema = yup.object().shape({
  categories: yup
    .array()
    .of(yup.number())
    .min(1, 'Please Select at least one job type!'),
  city: yup.string().required('Please add city!'),
  description: yup.string().required('Please add Job Description!'),
});





const defaultValues = {
  city: '',
  description: '',
  categories: [],
};

const JobPostForm: FC = () => {
  const {
    handleSubmit,
    control,
    formState: { errors },
    setValue,
    watch,
    reset,
    setError,
    clearErrors
  } = useForm<RegisterFormTypes>({
    resolver: yupResolver(schema),
    mode: 'onChange',
    defaultValues,
  });

  const { enqueueSnackbar } = useSnackbar();

  const { language } = useLanguage();

  // useEffect(()=>{
  //   if(watch("categories").length === 0){
  //     setError("categories", {
  //       type: "manual",
  //       message: "Please Select at least one job type!",
  //     })
  //   }else if(watch("categories").length > 0){
  //     clearErrors("categories")
  //   }
  // },[watch("categories")])

  const categories = watch('categories');

  const { data: jobTypesList = [] } = useQuery(
    ['type_of_staff', language, websiteId],
    () =>
      axios.get(
        `/category/sub_categories_published/${language}/${websiteId}?categoryType=CaringDay.Model.Employee`
      ),
    {
      select: (res) => {
        return res.data.data.map((val: any) => {
          return {
            value: val.id,
            label: val.name,
          };
        });
      },
      enabled: !!language,
    }
  );

  function generateUniqueName(name: string) {
    if (name) {
      const unique = name.toLowerCase().replace(/[^A-Z0-9]+/gi, '.');
      return unique;
    }
    return name;
  }

  const submitForm = (formVales: RegisterFormTypes) => {
    var dataToSend = {
      ...formVales,
      language: language,
      unique_name: generateUniqueName(formVales?.description),
    };

    if(formVales?.categories?.length === 0 || !formVales.categories){
      enqueueSnackbar("Please select at least one job type", { variant: 'error' });
    return
    }

    axios({
      url: `/job/web`,
      method: 'POST',
      data: dataToSend,
    })
      .then((res) => {
        if (res.data.success) {
          enqueueSnackbar('Job Added Successfully..!', {
            variant: 'success',
          });
          reset(defaultValues);
        } else {
          enqueueSnackbar(res?.data?.message, { variant: 'error' });
        }
      })
      .catch((err) => {
        enqueueSnackbar(err?.response?.data?.message, { variant: 'error' });
      });
  };

  const checkBoxChange = (tradId: number) => {
    if (_.includes(categories, tradId)) {
      const newCategories = _.reject(categories, (val) => val === tradId);
      setValue('categories', newCategories);
    } else {
      setValue('categories', [...categories, tradId]);
    }
  };

  return (
    <div className="container mx-auto flex flex-col items-center justify-center mt-32 lg:mt-16 py-16">
      <Container
        className="w-11/12 lg:w-6/12 mb-8"
        onSubmit={handleSubmit(submitForm)}
      >
        <div className="w-full mb-9">
          <StyledText fs={20} lh={39} color="#DA3B19">
            Post a job
          </StyledText>
        </div>
        <div className="w-full flex flex-wrap mb-5">
          <div className="w-full mb-2">
            <div className="flex items-center">
              <StyledText className="mb-2">Type of Job</StyledText>
              {Boolean(get(errors, 'categories')) && (
                <StyledText
                  className="ml-1"
                  color="red"
                  fs={13}
                  lh={15}
                  fw={500}
                  ls={0}
                >
                  ({(get(errors, 'categories') as any)?.message})
                </StyledText>
              )}
            </div>
          </div>
          {jobTypesList?.map((val: any) => (
            <div className="w-full flex items-center mb-2" key={val.value}>
              <Checkbox
                checked={_.includes(categories, val.value)}
                onChange={() => checkBoxChange(val.value)}
              />
              <StyledText color="#000" fs={20} lh={20} fw={400} ls={0}>
                {val.label}
              </StyledText>
            </div>
          ))}
        </div>
        <div className="w-full flex flex-wrap mb-5">
          <HookTextGlobalInput
            name="description"
            control={control}
            errors={errors}
            label="Job Description"
          />
        </div>
        <div className="w-full flex flex-wrap mb-5">
          <HookTextGlobalInput
            name="city"
            control={control}
            errors={errors}
            label="City"
          />
        </div>
        <div className="flex w-6/12 items-center justify-center">
          <StyledButton type="submit" className="px-20 py-4">
            <StyledText fs={17} fw={700} ls={0.3} color="white">
              Post
            </StyledText>
          </StyledButton>
        </div>
      </Container>
    </div>
  );
};

export default JobPostForm;

const Container = styled.form`
  padding: 32px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #00000012;
`;
