import axios from 'axios';
import { FC } from 'react';
import { useQuery } from 'react-query';
import { useParams } from 'react-router-dom';
import Footer from '../../shared/Footer';
import Header from '../../shared/Header';
// @ts-ignore
import renderHTML from 'react-render-html';
import useLanguage from '../../shared/hooks/useLanguage';

const GenericPage: FC = () => {
  const { unique_name } = useParams();
  const { language } = useLanguage();

  const { data: content = '' } = useQuery(
    ['right-reserved', language, unique_name],
    async () =>
      await axios.get(
        `/generic_content/unique_name/${unique_name}/${language}`
      ),
    {
      select: (res: any) => {
        return res.data.data.contente;
      },
      enabled: !!language,
    }
  );

  return (
    <div>
      <Header />
      <div className="container mx-auto flex flex-wrap mt-20 py-14">
        {renderHTML(content || '')}
      </div>
      <Footer />
    </div>
  );
};

export default GenericPage;
